@import "../component.scss";

.header{
	&-top{
		position: relative;
		max-width: 1903px;
		height: 110px;
		margin: 0 auto;
		h1{
			padding-top: 49px;
			text-align: center;
		}
		.btn-menu{
			position: absolute;
			top: 64px;
			left: 30px;
			color: #000;
			font-size: 11px;
		}
		.util{
			position: absolute;
			top: 64px;
			right: 30px;
			color: #000;
			font-size: 11px;
		}
	}
	.gnb-wrap{
		position: relative;
		height: 60px;
		background-color: #0061a0;
		nav{
			width: 1200px;
			text-align: center;
			margin: 0 auto;
		}
		ul{
			@include clear;
			display: inline-block;
		}
		.gnb{
			float: left;
		}
		.depth-1{
			position: relative;
			display: inline-block;
			width: 200px;
			height: 60px;
			line-height: 60px;
			color: #fff;
			text-align: center;
			vertical-align: top;
			a{
				display: block;
				color: inherit;
				font-weight: 700;
				font-size: 16px;
			}
		}
		.gnb{
			&:hover{
				.depth-1{
					background-color: #0090da;
					&:after{
						content: '';
						position: absolute;
						bottom: 0;
						left: 50%;
						display: inline-block;
						margin-bottom: -7px;
						margin-left: -7px;
						border-bottom: 15px solid #fff;
						border-left: 15px solid #fff;
						transform: rotate(45deg);
					}
				}
			}
		}
		.depth-2{
			position: absolute;
			left: 0;
			right: 0;
			display: block;
			height: 181px;
			background: #fff;
			z-index: 1;
			ul{
				@include clear;
				width: 1200px;
				margin: 0 auto;
				box-sizing: border-box;
			}
			li{
				float: left;
				a{
					display: block;
					width: 200px;
					height: 181px;
					text-align: center;
					font-size: 14px;
					font-weight: 700;
					color: #000;
					box-sizing: border-box;
					&:hover{
						color: #0090da;
						background-color: #fdfdfd;
					}
				}
			}
		}
		.gnb-1 .depth-2 ul,
		.gnb-2 .depth-2 ul{
			padding-left: 200px;
		}
		.gnb-3 .depth-2 ul{
			padding-left: 400px;
		}
	}
}